.auth {
  margin: 3rem auto;
  width: 50%;
  /* max-width: 25rem; */
  border-radius: 6px;
  background-color: #13371d;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  padding: 1rem;
  text-align: center;
  color: white;
}

.auth h1 {
  text-align: center;
  color: white;
}

.control {
  margin-bottom: 0.5rem;
}

.control label {
  display: block;
  color: white;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.control input {
  font: inherit;
  background-color: #5ed77c;
  color: #13371b;
  border-radius: 4px;
  border: 1px solid white;
  width: 100%;
  text-align: left;
  padding: 0.25rem;
}

.actions {
  margin-top: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.actions button {
  cursor: pointer;
  font: inherit;
  color: white;
  background-color: #0d4f20;
  border: 1px solid #41c87b;
  border-radius: 4px;
  padding: 0.5rem 2.5rem;
}

.actions button:hover {
  background-color: #70fb85;
  border-color: #1cf865;
}

.actions .toggle {
  margin-top: 1rem;
  background-color: transparent;
  color: #3bf331;
  border: none;
  padding: 0.15rem 1.5rem;
}

.actions .toggle:hover {
  background-color: transparent;
  color: #0afa16;
}

.camera {
  width: 100%;
  padding: 10px;
}

.cameraform {
  padding: 10px;
  width: 100%;
}
